const baseURL = 'http://api.cloudmusic.pulsating.cn:3000';

const request = {
	get(url, data = null) {
		uni.showLoading({
			title: ''
		});
		return new Promise((resolve, reject) => {
			uni.request({
				url: baseURL + url,
				data: data,
				header: {
					'Accept': 'application/json',
					'Content-Type': 'application/x-www-form-urlencoded', //
				},
				method: "GET",
				success: (response) => {
					uni.hideLoading();
					let res = response.data
					if (res.code === 200) {
						resolve(res)
					} else {
						showError(res);
					}
				},
				fail: (error) => {
					uni.hideLoading();
					if (error && error.response) {
						reject(error.response)
						showError(error.response);
					}
				},
				complete: () => {
					setTimeout(function() {
						uni.hideLoading();
					}, 250);
				}
			});
		})

	},
	post(url, data = null) {
		uni.showLoading({
			title: ''
		});
		return new Promise((resolve, reject) => {
			uni.request({
				url: baseURL + url,
				data: data,
				header: {
					'Accept': 'application/json',
					'Content-Type': 'application/json',
				},
				method: "POST",
				success: (response) => {
					let res = response.data
					if (res.code === 200) {
						resolve(res)
					} else {
						showError(res);
					}
				},
				fail: (error) => {
					uni.hideLoading();
					if (error && error.response) {
						reject(error.response)
						showError(error.response);
					}
				},
				complete: () => {
					uni.hideLoading();
				}
			});
		})
	},
}

const showError = error => {
	let errorMsg = ''
	switch (error.code) {
		case 400:
			errorMsg = ''
			break
		case 301:
			errorMsg = ''
			break
		case 403:
			errorMsg = ''
			break
		case 404:
			errorMsg = ''
			break
		case 408:
			errorMsg = ''
			break
		case 500:
			errorMsg = ''
			break
		case 501:
			errorMsg = ''
			break
		case 502:
			errorMsg = ''
			break
		case 503:
			errorMsg = ''
			break
		case 504:
			errorMsg = ''
			break
		case 505:
			errorMsg = 'HTTP'
			break
		default:
			errorMsg = ""
			break
	}

	uni.showToast({
		title: errorMsg,
		icon: 'none',
		duration: 2000,
		complete: function() {
			setTimeout(function() {
				uni.hideToast();
			}, 2000);
		}
	});
}

export default request
